import { connect } from 'react-redux';
import { reduxForm, initialize } from 'redux-form';

import { UsersRoles } from '../../components';

import { validateRole as validate } from '../../util/validator';

import { rolesSelector } from '../../selectors';

import {
  fetchRolesRequested,
  createRoleRequested,
  updateRoleRequested,
  updateRoleStatusRequested
} from '../../redux/roles/actions';

const mapStateToProps = state => ({
  roles: rolesSelector(state)
});

const mapDispatchToProps = {
  updateRoleStatusRequested,
  fetchRolesRequested,
  createRoleRequested,
  updateRoleRequested,
  initialize
};

export default reduxForm({
  form: 'createRole',
  fields: ['name'],
  validate,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(UsersRoles));



