import { connect } from 'react-redux';
import { reduxForm, initialize } from 'redux-form';

import {
  fetchDictionaryServiceKindRequested,
  updateDictionaryServiceKindRequested,
  createDictionaryServiceKindRequested,
  updateDictionaryServiceKindStatusRequested
} from '../../../redux/dictionaries/service/kind/actions';

import { DictionaryList } from '../../../components/index';

import { validateDictionaryServiceKindItem as validate } from '../../../util/validator';

const mapStateToProps = state => ({
  data: state.dictionaries.serviceKind
});

const mapDispatchToProps = {
  fetchRequested: fetchDictionaryServiceKindRequested,
  updateRequested: updateDictionaryServiceKindRequested,
  createRequested: createDictionaryServiceKindRequested,
  updateStatusRequested: updateDictionaryServiceKindStatusRequested,
  initialize
};
export default reduxForm({
  form: 'createDictionaryServiceKind',
  fields: ['refCode', 'name'],
  validate,
  destroyOnUnmount: false
})(connect(mapStateToProps, mapDispatchToProps)(DictionaryList));



