import React, { PureComponent } from 'react';
import { Col, Row } from 'reactstrap';

import {
  UsersFilters,
  UsersRoles,
  UsersList
} from '../../containers';

class Page extends PureComponent {

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <UsersFilters />
          </Col>
        </Row>
        <Row>
          <Col className="col-right" md="4" lg="4">
            <UsersRoles />
          </Col>
          <Col className="col-left" md="8" lg="8">
            <UsersList />
          </Col>
        </Row>
      </div>
    );
  }
}

export default Page;



